/**
 * Created by gupo-03 on 2017/3/22.
 * getRefresh:
 *  this.PubMethod.refresh.getScrollTop(el, this, )
 * datascollpage: 1(),scollsite: 'top'()
 *
 * getTop:
 * datascollsite: 'top'()
 *  this.PubMethod.refresh.getScrollTop(el, this, string())
 */
const refresh = {
  getRefresh (el, thi, reload) {
    el.addEventListener('touchend', function () {
      var top = el.scrollTop
      var totalScroll = el.scrollHeight
      var currentScroll = top + el.offsetHeight
      if (top === 0) {
        if (thi.scollsite === 'top') {
          if (thi.scollpage > 1) {
            thi.scollpage -= 1
          }
          reload()
        }
        thi.scollsite = 'top'
      } else if (currentScroll >= totalScroll) {
        if (thi.scollsite === 'bottom') {
          thi.scollpage += 1
          reload()
        } else {
          thi.scollsite = 'bottom'
        }
      } else {
        thi.scollsite = 'center'
      }
    })
  },
  getTop (el, thi, reload) {
    el.addEventListener('touchend', function () {
      var top = el.scrollTop
      if (top === 0) {
        if (thi.scollsite === 'top') {
          thi.$emit(reload)
        }
        thi.scollsite = 'top'
      } else {
        thi.scollsite = 'center'
      }
    })
  }
}
export default refresh



